var loginName = /^[cC]\w{4,10}/;
var phoneNum = /^1\d{10,11}$/;
var password = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
var parentId = /^\d{10}/;
export function validateLoginName(str) {
  return loginName.test(str);
}
export function validatePhoneNum(str) {
  return phoneNum.test(str);
}
export function validatePassword(str) {
  return password.test(str);
}
export function validateParentId(str) {
  return parentId.test(str);
}
export default {
  validateLoginName: validateLoginName,
  validatePhoneNum: validatePhoneNum,
  validatePassword: validatePassword,
  validateParentId: validateParentId
};